.app {
  margin: 0 auto;
  padding: 0 0 30px;
  min-height: 100vh;
  background-color: #ccc;
  font-family: "Arial", "Helvetica", sans-serif;
}

.app__header {
  margin: 0 auto;
  padding: 20px 10px 15px;
  max-width: 500px;
}

.app__content {
  margin: 0 auto;
  padding: 10px;
  max-width: 768px;
  display: flex;
  justify-content: center;
  align-content: center;
}

.app__footer {
  margin: 0;
  padding: 0;
  max-width: 768px;
  position: fixed;
  right: 0;
  bottom: 0;
  left: 0;
  color: #999;
  background-image: linear-gradient(to top, #333, #ccc);
}

@media screen and (min-width: 768px) {
  .app__footer {
    padding-right: calc(50% - 384px);
    padding-left: calc(50% - 384px);
  }
}

.app__copyright {
  margin: 10px;
  padding: 0;
  font-size: 12px;
  line-height: 14px;
  font-style: normal;
  font-weight: normal;
}
